.container {
  display: grid;
}

.inner {
  margin: auto;
}

.video {
  display: block;
  border-radius: var(--border-radius-sm);
  box-shadow: var(--box-shadow-white);
}

.text {
  max-width: 600px;
  margin: auto;
  margin-top: 100px;
  line-height: 1.7;
  font-size: 26px;
  text-align: center;
  text-shadow: 0 0 2px var(--white-color);
  color: var(--theme-default-color);
}
